
.layout{
    width: 50%;
    margin: 0 auto;
}
.ordenPedido{
    text-align: center;

    h3{
        color: #848484;
        margin-bottom: 0px;
        font-weight: 400;
        font-size: 20px;
        letter-spacing: 1.2px;
    }
    h2{
        color: #848484;
        margin-top: 0px;
        font-weight: 800;
        font-size: 30px;
        letter-spacing: 1px;
    }
}
.compra{
    margin-top: 30px;
    h3{
        text-align: center;
        position: relative;
        color: #707070;
        letter-spacing: 1.5px;

        &::before{
            content: '';
            background-color: #E43330;
            position: absolute;
            bottom: -11px;
            height: 6px;
            width: 32%;
            border-radius: 10px;
        }
    }
}
.entrega{
    text-align: center;

    .cajaCntent{
        width: 100%;
        box-sizing: border-box;
        padding: 20px;
        text-align: center;
    
        img{
            width: 60%;
        }
    }
    hr{
        width: 80%;
    }
    h2{
        color: #707070;
        font-size: 5vw;
        
           
        .pinclass{
                font-size: 35px;
                color: #413f3f;
                
        }
    }
    .recuerda{
        width: 100%;
        background-color: rgba(230, 183, 81, 0.26);
        box-sizing: border-box;
        padding: 5px 9vw;
        margin-bottom: 10vw;
        color: #fff;
        position: relative;
       

        &::before{
            content: "";
            position: absolute;
            left: 0px;
            top: 0px;
            width: 10px;
            height: 100%;
            background-color: #E6B851;
            border-radius: 0px 10px 10px 0px;
        }
        p{
            color: #E6B851;
            font-size: 23px;
            span{
                font-weight: 700;
            }
        }
    }
}
.datesNuew{
    display: none;
}
.footer{
    background-color: #E43330;
    box-sizing: border-box;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    color: #fff;
    a{
        color: #fff;
        text-decoration: none;
        font-size: 20px;
    }
    img{
        width: 40%;
        height: 40%;
    }
}
@media (min-width: 300px) and (max-width: 767px) {
    .layout{
        width: 100%;
        margin: 0 auto;
    }
}
