.calendar{
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    .content-calendario{
        width: 90%;
        box-shadow: 0px -1px 9px -1px rgba(0,0,0,0.28);
        border-radius: 25px;
        text-align: center;
        .header-date{
            background-color: #D8232A;
            box-sizing: border-box;
            padding: 2px;
            text-align: center;
            color: #fff;
            border-radius: 25px 25px 0px 0px;

            p{
                text-align: left;
                padding-left: 10px;
            }
            h2{
                text-align: left;
                font-size: 29px;
                margin-bottom: 10px;
                font-weight: 500;
                padding-left: 13px;
                margin-top: 10px;
            }
        }
        .react-datepicker{
             
            width: 100%;
            border-radius: 0px 0px 25px 25px;
        }
        
        .datepicker---date-picker-container---1kNBY .react-datepicker__day, .datepicker---date-picker-container---1kNBY .react-datepicker__day-name{
            padding: 2.5vw;
            width: 12.5%;
            height: 5.5vw;
        }
        .btncalendar{
            background-color: #00ABB0;
            width: 70%;
            border: none;
            border-radius: 50px;
            box-sizing: border-box;
            padding: 15px 30px;
            color: #fff;
            font-size: 15px;
            font-weight: 400;
            margin-bottom: 15px;
            box-shadow: 0px 7px 20px -5px rgb(5, 170, 176);
            outline: none;
            cursor: pointer;
            a{
                text-decoration: none;
                color: #ffffff;
                font-size: 20px;
                font-weight: 500;
            }
        }
    }
    
}
.asesoraMsj{
    width: 100%;
    text-align: center;

    img{
        width: 90%;
    }
}
.globo{
    height: 200px;
    box-sizing: border-box;
    padding: 10px;
    background-size: cover;
    position: absolute;
    width: 100%;
    text-align: center;
    .contenIMg{
        position: relative;
        width: 100%;
        .globo__img{
           
            box-sizing: border-box;
            padding-right: 20px;
            left: 2vw;
            position: absolute;
            width: 46%;
            top: 24px;
        }
    }

    
    &__info{
        position: relative;

        p{
            padding: 41px 73px;
            font-size: 2.3vw;
            color: #ffffff;
            font-weight: 600;
            letter-spacing: 1.7px;
            text-align: left;
            width: 41%
        }
    }
}
.listCompra{
    list-style: none;
    padding-left: 5px;
    margin-top: 30px;
//    border-bottom: dashed 1px #707070;
    width: 90%;
    margin: 0 auto;

    li{
        margin-bottom: 10px;
        color: #707070;
        
        span{
            color: #A2A2A2;
            font-weight: 500;
        }
    }
    .productName{
        font-size: 22px;
        box-sizing: border-box;
        padding: 10px;
        padding-left: 0px;
        color: #6E6E6E;
    }
}
.listCompra:last-child{
    border-bottom: none;
    margin-bottom: 20vw;
}
@media (min-width: 300px) and (max-width: 767px) {
    
    .calendar .content-calendario .datepicker---date-picker-container---1kNBY .react-datepicker__day, .calendar .content-calendario .datepicker---date-picker-container---1kNBY .react-datepicker__day-name{
        height: 11vw;
    }
    

    .globo .contenIMg .globo__img{
        width: 100%;
        top: 0px
    }
    .globo__info{
        p{

            font-size: 4.3vw;
            width: 100%;
            padding: 10px;
            box-sizing: border-box;
            position: relative;
            bottom: 1vw;
            left: 2vw
        }
    }
}